//global style
.main-wrapper {
	width: 100%;
	overflow: hidden;
}

body {
	font-family: "Cairo", sans-serif;

	.main-wrapper {
		width: 100%;
	}
}

//global style

//header
header {
	background-color: $main_color;
	position: static;
	top: 80px;
	width: 100%;
	z-index: 999;

	.navbar-light {
		&.navbar {
			padding: 15px 0;

			.buttons {
				order: 2;
				display: flex;
				justify-content: flex-end;
				align-items: center;

				.butt {
					margin-right: 10px;

					.btn {
						color: #fff;
						padding: 5px 25px;
					}

					&.appoint {
						.btn {
							text-transform: uppercase;
							background-color: $main_color;
							border-color: $main_color;

							&:hover {
								color: $second_color;
							}
						}

						.hvr-sweep-to-top:before {
							background: #fff;
						}
					}

					&.call {
						.btn {
							text-transform: capitalize;
							background-color: transparent;
							border-color: #fff;
							border-radius: 10px;
							overflow: hidden;

							&:hover {
								color: $second_color;
							}
						}

						.hvr-sweep-to-top:before {
							background: #fff;
						}
					}

					&:last-child {
						margin-right: 0;
					}
				}
			}

			.navbar-collapse {
				max-width: 690px;
			}
		}

		.navbar-nav {
			padding: 0;

			.nav-link {
				color: #fff;
				text-transform: uppercase;
				font-size: 14px;
				padding: 7px 15px;

				&.active,
				.show > .nav-link {
					color: $second_color;
				}

				&:hover,
				&:focus {
					color: $second_color;
				}
			}
		}
	}

	.navbar-toggler {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		box-shadow: none;
		border: 0;

		span {
			display: block;
			width: 40px;
			height: 2px;
			border-radius: 3px;
			background-color: #fff;
			position: relative;

			&:before {
				content: "";
				position: absolute;
				top: 0px;
				left: 0;
				width: 30px;
				height: 2px;
				border-radius: 3px;
				background: #fff;
				margin-top: -7px;
				transition: all 0.3s ease;
				-webkit-transition: all 0.3s ease;
				-moz-transition: all 0.3s ease;
				-ms-transition: all 0.3s ease;
				-o-transition: all 0.3s ease;
			}

			&:after {
				content: "";
				position: absolute;
				top: 0px;
				left: 0;
				width: 30px;
				height: 2px;
				border-radius: 3px;
				background: #fff;
				margin-top: 7px;
				transition: all 0.3s ease;
				-webkit-transition: all 0.3s ease;
				-moz-transition: all 0.3s ease;
				-ms-transition: all 0.3s ease;
				-o-transition: all 0.3s ease;
			}
		}

		&:focus {
			outline: none;
			border: 0;
			box-shadow: none;
		}

		&.close {
			span {
				transform: rotate(50deg);
				-webkit-transform: rotate(50deg);
				-moz-transform: rotate(50deg);
				-ms-transform: rotate(50deg);
				-o-transform: rotate(50deg);
				width: 25px;

				&::before {
					width: 25px;
					transform: translateY(7px) rotate(80deg);
					-webkit-transform: translateY(7px) rotate(80deg);
					-moz-transform: translateY(7px) rotate(80deg);
					-ms-transform: translateY(7px) rotate(80deg);
					-o-transform: translateY(7px) rotate(80deg);
				}

				&::after {
					display: none;
				}
			}
		}
	}

	&.fixed-header {
		position: fixed;
		top: 0;
		width: 100%;
		z-index: 999;
	}
}

//header

//modals
.modals {
	.modal-dialog {
		margin: 2rem auto;
	}

	.success-modal,
	.failed-modal {
		text-align: center;
		.modal-content {
			border-radius: 10px;
		}
		.icon {
			width: 80px;
			height: 80px;
			background-color: $main_color;
			@extend %center;
			color: #fff;
			border-radius: 50%;
			margin: auto;
			margin-top: -60px;
			margin-bottom: 20px;

			i {
				font-size: 50px;

				&::before {
					vertical-align: middle;
				}
			}
		}
		.text {
			.modal-title {
				text-transform: uppercase;
				font-size: 22px;
				margin-bottom: 15px;
			}
			p {
				font-size: 18px;
				color: #222;
				text-transform: capitalize;
			}
		}
		.modal-footer {
			justify-content: center;
			.btn {
				background-color: $main_color;
				padding: 10px 30px;
				border: none;
			}
		}
	}

	.failed-modal {
		.icon {
			background-color: red;
			i {
				font-size: 30px;
			}
		}
		.modal-footer {
			.btn {
				background-color: red;
			}
		}
	}
}

