	background-size: cover;
	background-position: center;
	position: relative;
	z-index: 1;

	&::after {
		content: "";
		position: absolute;
		background-color: $main_color;
		opacity: 0.3;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		z-index: -1;
	}

	.content {
		text-align: center;

		.logo {
			margin-bottom: 30px;
			img {
