					// 		content: "\F633";
					// 		position: absolute;
					// 		left: 16px;
					// 		top: 10px;
					// 		background-color: $main_color;
					// 		display: inline-block;
					// 		font-style: normal;
					// 		font-variant: normal;
					// 		text-rendering: auto;
					// 		-webkit-font-smoothing: antialiased;
					// 		width: 16px;
					// 		height: 16px;
					// 		text-align: center;
					// 		line-height: 16px;
					// 		font-size: 10px;
					// 		vertical-align: middle;
					// 		border-radius: 50%;
					//   }
				}
			}
		}

		&:hover {
			box-shadow: 0 3px 12px 0 rgba(0, 0, 0, 0.16);
			border-bottom-color: $second_color;

			.icon {
