//emergency
section.emergency {
	background-position: center;
	background-size: cover;
	position: relative;
	z-index: 1;

	&:hover {
		&::before {
			opacity: 1;
		}

		&::after {
			opacity: 1;
		}
	}

	&::before {
		@extend %overlay;
		width: 50%;
		background-color: $main_color;
		opacity: 0.8;
		z-index: -1;
		transition: all ease 0.3s;
	}

	&::after {
		@extend %overlay;
		width: 50%;
		right: 0;
		left: auto;
		background-color: $red_color;
		opacity: 0.8;
		z-index: -1;
		transition: all ease 0.3s;
	}

	.emergency-content {
		display: grid;
		grid-template-columns: repeat(2, 50%);
		padding: $padding_section;

		.emergency-box {
			p {
				margin-bottom: 0;
			}

			&:first-child {
				padding-inline-end: 30px;
			}

			&:last-child {
				padding-inline-end: 30px;
				padding-inline-start: 50px;
			}

			h3 {
				color: #fff;
				font-weight: 600;
				margin-bottom: 25px;
			}

			p {
				color: #fff;
				line-height: 1.8;
			}

			.button {
				padding-top: 30px;
				.btn {
					@extend %btn_selector;
					background-color: $second_color;
					padding: 15px 35px;
					font-weight: 700;
					border-color: #fff;
				}
			}
		}
	}
}
