//services
section.services {
	background-color: #f8f8f8;
	padding: $padding_section;

	.title {
		p {
			max-width: 545px;
		}

		.more {
			text-align: right;

			.btn {
				color: $main_color;
				border-color: $main_color;
				border-radius: 10px;
				font-size: 16px;
			}
		}
	}

	.service-box {
		border-radius: 10px;
		overflow: hidden;
		border: 1px solid rgb(206 206 206 / 20%);
		text-align: center;
		margin-bottom: 25px;

		.image {
			display: flex;
			align-items: center;
			height: 230px;
			overflow: hidden;

			img.serv-img {
				width: 100%;
				min-height: 100%;
				-o-object-fit: cover;
				object-fit: cover;
				max-height: 100%;
				transition: all ease 0.3s;
			}
		}

		.details {
			background-color: $main_color;
			padding: 15px;
			padding-top: 30px;
			position: relative;

			h3 {
				color: #fff;
				text-transform: uppercase;
				font-size: 18px;
				font-weight: 700;
				// margin-bottom: 15px;
				overflow: hidden;
				// text-overflow: ellipsis;
				// display: -webkit-box;
				// -webkit-line-clamp: 1;
				// -webkit-box-orient: vertical;
			}

			p {
				color: #fff;
				font-size: 14px;
				overflow: hidden;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 3;
				-webkit-box-orient: vertical;
			}

			.clickable {
				text-align: center;

				.btn {
					border-radius: 10px;
					background-color: transparent;
					border-color: #fff;
					color: #fff;
					text-transform: capitalize;
					font-size: 16px;
					padding: 7px 30px;
					overflow: hidden;
				}
			}

			.icon {
				position: absolute;
				left: 15px;
				top: -35px;
				height: 70px;
				width: 70px;
				text-align: center;
				background-color: #fff;
				line-height: 70px;
				box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.16);

				img {
					width: 40px;
				}
			}
		}

		&:hover {
			.image {
				img.serv-img {
				}
			}
		}
	}
}
