section.contact-info {
	background-color: #fff;
	padding: 80px 0 0;

	.info-area {
		z-index: 9;
		position: relative;

		.box {
			margin-bottom: 25px;

			&:nth-child(3),
			&:nth-child(4) {
				margin-bottom: 0;
			}
		}

		.info-box {
			position: relative;
			z-index: 1;
			background-color: #fff;
			border: 1px solid rgba(89, 86, 232, 0.149);
			border-radius: 7px;
			padding: 40px 35px 30px;
			transition: all 0.3s;
			height: 100%;

			&:hover {
				background-color: $second_color;
				border-color: rgba(249, 153, 51, 0.1);

				i.big {
					border-color: #fff;
					background-color: $main_color;
				}

				h3 {
					color: #fff;
				}

				span {
					color: #fff;
				}

				a,
				p {
					color: #fff;
				}
			}

			h3 {
				font-size: 24px;
				color: $dark_color;
				font-weight: 600;
				text-transform: capitalize;
			}

			i.big {
				font-size: 22px;
				text-align: center;
				color: #fff;
				margin-bottom: 20px;
				background-color: $second_color;
				width: 60px;
				height: 60px;
				border-radius: 50%;
				display: flex;
				align-items: center;
				justify-content: center;
				transition: all ease 0.8s;
				border: 2px solid $second_color;
			}

			span {
				display: block;
				color: $gray;
				font-size: 16px;
			}

			a {
				color: $gray;
				text-transform: capitalize;
				display: block;
				font-size: 16px;
			}
			.social {
				display: flex;
				padding-top: 15px;

				a {
					margin-inline-end: 15px;
				}
				i {
					font-size: 20px;
				}
			}
		}
	}

	.map {
		line-height: 0;
		height: 100%;
		overflow: hidden;
		border-radius: 5px;
	}
}

