					height: auto !important;
				}
			}
		}

		.contact-btn {
			text-align: center;

			.btn {
				@extend %btn_selector;
			}
		}

		.successfully {
			font-size: 16px;
			margin-bottom: 25px;
			color: $second_color;
		}
	}
}
html[dir="rtl"] .form-control.is-invalid,
html[dir="rtl"] .was-validated .form-control:invalid {
	background-position: left calc(0.375em + 0.1875rem) center;
}
