@media screen and (max-width: 991px) {
	header {
		.navbar-light.navbar {
			.buttons {
				order: unset;
				margin-right: 10px;
			}

			.navbar-nav {
				padding-top: 25px;
			}
		}
	}
	section.emergency {
		&::before {
			width: 100%;
			bottom: 0;
			top: auto;
			height: 50%;
		}

		&::after {
			width: 100%;
			top: 0;
			height: 50%;
		}

		.emergency-content {
			display: block;
			padding: 0;

			.emergency-box {
				.button {
					padding-bottom: 30px;
			  }
				&:first-child {
					padding: 50px 0;
				}

				&:last-child {
					padding: 50px 0;
				}
			}
		}
	}
	section.contact-info {
		.info-area {

			.box:nth-child(3),
			.box:nth-child(4) {
				margin-bottom: 25px;
			}
		}

		.map {
			height: 300px;
		}
	}

	section.contact-in-page {
		.content {
			padding: 50px 25px;
		}
	}
}

@media screen and (max-width: 767px) {
	.xdsoft_datetimepicker.xdsoft_inline {
		display: block;
		position: static;
		box-shadow: none;
  }
  .xdsoft_datetimepicker .xdsoft_datepicker.active+.xdsoft_timepicker {
	margin-inline-start: 0;
}
}

